.add-sapphire-todo-container {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	right: 0;
	z-index: 99999999;


	iframe {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		right: 0;
	}

	.add-todo-back-btn {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 5;
		height: 61px;
		width: 60px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
}

.edit-sapphire-todo {
	display: none;

	&.--show-todo-edit {
		opacity: 1;
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		right: 0;
		z-index: 99999999;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #fff;

		iframe {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			right: 0;
			display: block;
		}

		.add-todo-back-btn {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 5;
			height: 61px;
			width: 60px;
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}

	//.add-todo-back-btn {
	//    display: none;
	//}
	//
	//iframe {
	//    display: none;
	//}
}
